/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;

  &:focus {
    outline: none;
  }

  &.dragging {
    cursor: pointer;
    cursor: hand;
  }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  @include transform(translate3d(0, 0, 0));
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;

  &:before,
  &:after {
    content: "";
    display: table;
  }

  &:after {
    clear: both;
  }

  .slick-loading & {
    visibility: hidden;
  }
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  [dir="rtl"] & {
    float: right;
  }
  img {
    display: block;
  }
  &.slick-loading img {
    display: none;
  }

  display: none;

  &.dragging img {
    pointer-events: none;
  }

  .slick-initialized & {
    display: block;
  }

  .slick-loading & {
    visibility: hidden;
  }

  .slick-vertical & {
    display: block;
    height: auto;
    border: 1px solid transparent;
  }
}
.slick-arrow.slick-hidden {
  display: none;
}


/* Arrows */

.slick-prev {
  left: rem(5px);
  [dir="rtl"] & {
    left: auto;
    right: rem(5px);
  }
  &:before {
    @extend .icon-chevron-left;
    [dir="rtl"] & {
      @extend .icon-chevron-right;
    }
  }
}

.slick-next {
  right: rem(5px);
  [dir="rtl"] & {
    left: rem(5px);
    right: auto;
  }
  &:before {
    @extend .icon-chevron-right;
    [dir="rtl"] & {
      @extend .icon-chevron-right;
    }
  }
}

.slick-prev,
.slick-next {
  @include transform(translate(0, -50%));
  border-radius: 100%;
  position: absolute;
  display: block;
  height: rem(55px);
  width: rem(55px);
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 25%;
  padding: 0;
  border: none;
  outline: none;
  z-index: 2;

  &:before {
    @extend %icons;
    font-size: rem(15px);
    color: $gray;
  }

  .no-touchevents & {
    &:hover, &:focus {
      outline: none;
      background: rgba(229,229,229, 0.6);
      color: transparent;

      &:before {
        opacity: 1;
        color: $white;
      }
    }
  }

  .bg-color--blue & {
    .no-touchevents & {
      &:hover, &:focus {
        background: rgba(229,229,229, 0.4);
      }
    }
  }

  &.slick-disabled:before {
    opacity: 0.2;
  }

  .slider--image & {
    top: 50%;
  }

  @include breakpoint($tablet) {
    top: 50%;
    height: rem(75px);
    width: rem(75px);

    &:before {
      font-size: rem(35px);
    }
  }
}

.slick-prev {
  left: rem(5px);
  [dir="rtl"] & {
    left: auto;
    right: rem(5px);
  }
  &:before {
    @extend .icon-chevron-left;
    [dir="rtl"] & {
      @extend .icon-chevron-right;
    }
  }

  @include breakpoint($screen-sm) {
    left: rem(10px);

    [dir="rtl"] & {
      right: rem(10px);
    }
  }
}

.slick-next {
  right: rem(5px);

  [dir="rtl"] & {
    left: rem(5px);
    right: auto;
  }

  &:before {
    @extend .icon-chevron-right;
    [dir="rtl"] & {
      @extend .icon-chevron-right;
    }
  }

  @include breakpoint($screen-sm) {
    right: rem(10px);

    [dir="rtl"] & {
      left: rem(10px);
    }
  }
}

/* Dots */

.slick-dotted.slick-slider {
  margin-bottom: rem(30px);
}

.slick-dots {
  position: absolute;
  bottom: rem(-25px);
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;

  li {
    position: relative;
    display: inline-block;
    height: rem(10px);
    width: rem(10px);
    margin: 0 rem(5px);
    padding: 0;
    cursor: pointer;
    vertical-align: middle;

    button {
      border: rem(1px) solid $medium-gray;
      background: transparent;
      border-radius: 100%;
      display: block;
      height: rem(10px);
      width: rem(10px);
      outline: none;
      line-height: 0px;
      font-size: 0px;
      color: transparent;
      padding: 0;
      cursor: pointer;

      .no-touchevents & {
        &:hover, &:focus {
          outline: none;
          background: $medium-gray;
        }
      }
    }

    &.slick-active button {
      background: $medium-gray;
    }
  }

  .js-slider-thumb & {
    @include breakpoint($tablet) {
      @include display(flex);
      position: static;
      padding: rem(20px) rem(15px);
      justify-content: center;

      li {
        height: inherit;
        width: inherit;
        max-width: rem(60px);
        margin: 0 rem(20px);
        padding-bottom: rem(7px);
        -ms-flex: 0 1 auto;

        img {
          max-width: rem(60px);
          height: auto;
        }
      }

      .slick-active {
        @include colored-border;
      }
    }
  }
}
