/*
 * jQuery Basic Table
 * Author: Jerry Low
 */

table.bt {
  thead,
  tbody th {
    display: none;
  }

  tfoot th,
  tfoot td,
  tbody td {
    border: none;
    display: block;
    @include display(flex);
    vertical-align: top;

    /* IE 9 */
    float: left\9;
    width: 100%\9;

    &::before {
      content: attr(data-th) ": ";
      display: inline-block;
      -webkit-flex-shrink: 0;
      -ms-flex-shrink: 0;
      flex-shrink: 0;
      font-weight: bold;
      width: 11em;
    }

    &.bt-hide {
      display: none;
    }

    & .bt-content {
      vertical-align: top;
    }
  }
}

.bt-wrapper.active {
  max-height: rem(310px);
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
