@font-face {
  font-family: 'Neubau Grotesk Light';
  src: url('../fonts/nbgrotesk-light-webfont.woff') format('woff'),
	   url('../fonts/nbgrotesk-light-webfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Neubau Grotesk Regular';
  src: url('../fonts/nbgrotesk-regular-webfont.woff') format('woff'),
  	   url('../fonts/nbgrotesk-regular-webfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Neubau Grotesk Bold';
  src: url('../fonts/nbgrotesk-bold-webfont.woff') format('woff'),
	   url('../fonts/nbgrotesk-bold-webfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

