/**
 * @file
 * sass/partials/base/_elements.scss
 */

*, *::after, *::before {
  box-sizing: border-box;
}

html {
  background-color: $base-background-color;
}

img,
picture {
  margin: 0;
  max-width: 100%;
  height: auto;
}

picture source,
picture img {
  @include breakpoint($phone-only) {
    margin-left: auto;
    margin-right: auto;
  }
}

strong,
b {
  @include helvetica-arial-semibold;
}

.anchor-target:target:before {
    content: "";
    display: block;
    height: rem(65px);
    margin: rem(-65px) 0 0;
}

// View Application Library

.view-app-library {
  .views-row {
    border-bottom: rem(1px) solid $gray-c;
    color: $light-grey;
    font-size: rem(15px);
    padding: rem(10px);
  }
}

//
// Utility classes
// --------------------------------------------------

.bg--blue {
  background-color: $tk-blue;
}

.bg--white {
  background: $white;
}

// Floats
// -------------------------

.clearfix {
  @extend %clearfix;
}

.center-block {
  @extend %center-block;
}

.pull-right {
  float: right !important;
}

.pull-left {
  float: left !important;
}

// Extend wrappers for components that don't have grid gutters on mobile.
.row-sm {
  @extend %row-sm;
}

// Add additional horizontal paddings for tablet breakpoint.
.side-padd-md {
  @extend %side-paddings-md;
}

// Toggling content
// -------------------------
.show {
  display: block !important;
}

.invisible {
  visibility: hidden;
}

// Hide from screenreaders and browsers
.hidden {
  display: none !important;
}

.hidden-mobile {
  @include breakpoint($mobile-only) {
    display: none !important;
  }
}

.hidden-desktop {
  @include breakpoint($tablet) {
    display: none !important;
  }
}

//Visible only for screen readers
.sr-only {
  @include sr-only;
}

.date {
  @extend %date;
}

.border-right-md {
  @extend %border-right-md;
}

.align-icon {
  @extend %align-icon;
}

.video-icon {
  @extend %video-icon;
}

// Add this class to display flex items on multiple lines.
.multi-line {
  @extend %responsive-align-cards;
}

// Add this class to iframe's wrapper.
.responsive-video {
  @extend %responsive-video;
}
