/**
 * @file
 * sass/partials/base/_forms.scss
 *
 * Based on http://bitters.bourbon.io/
 */

fieldset {
  background-color: transparent;
  border: 0;
  margin: 0;
  padding: 0;
}

legend {
  margin-bottom: $small-spacing / 2;
  padding: 0;
}

input,
label,
select {
  display: block;
  font-family: $base-font-family;
}

label {
  font-size: rem(14px);
  margin-bottom: rem(5px);

  &.form-required::after {
    content: "*";
    margin-left: rem(3px);
  }

  abbr {
    display: none;
  }
}

form {
  .description {
    font-size: rem(12px);
    padding: rem(5px) rem(10px);
  }
}

#{$all-text-inputs},
select {
  background-color: $base-background-color;
  border: $base-border;
  box-shadow: $form-box-shadow;
  color: $light-grey;
  font-size: rem(14px);
  height: rem($inputHeight);
  padding: 0 rem(10px);
  transition: border-color $base-duration $base-timing;
  width: 100%;

  &:hover,
  &:focus {
    border-color: shade($base-border-color, 20%);
  }

  &:focus {
    box-shadow: none;
    outline: none;
  }

  &:disabled {
    background-color: shade($base-background-color, 5%);
    cursor: not-allowed;

    &:hover {
      border: $base-border;
    }
  }

  .touchevents & {
    font-size: rem(16px) !important; //avoid zooming on iphone
  }
}

// Add select arrow image, to look the same crossbrowser.
select {
  @include appearance(none);
  // Use converted to code image 'arrow-drop-down.svg'.
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: calc(100% - #{rem(10px)}) 50%;
  // Remove browser Select arrow on IE.
  &::-ms-expand {
    display: none;
  }
}

select[multiple=multiple] {
  height: auto;
  background: none;
  border-radius: rem(10px);
  padding: rem(10px);
  overflow: hidden; // Hide scroll bar as height is auto.
}

textarea {
  resize: vertical;
  line-height: rem(35px);
}

input[type="button"],
input[type="reset"],
input[type="submit"], 
.button-courseware a {
  @extend %btn;
}

input,
select {
  border-radius: rem(20px);
}

input[type="search"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  box-sizing: border-box;
}

input[type="file"] {
  margin-bottom: $small-spacing;
  width: 100%;
}

select {
  max-width: 100%;
  padding-right: rem(23px);
}

input[type=checkbox],
input[type=radio] {
  display: none;

  & + label {
    @include user-select(none);
    color: $lightest-grey;
    cursor: pointer;
    display: block;
    font-size: rem(11px);
    font-weight: normal;
    line-height: rem(18px);
    padding-left: rem(28px);
    position: relative;
    text-transform: uppercase;

    &:before {
      @extend %icons;
    }

    &:before,
    &:after {
      display: block;
      font-weight: bold;
      left: 0;
      position: absolute;
    }

    &:after {
      content: "";
      border: rem(1px) solid $lightest-grey;
      height: rem(18px);
      top: 0;
      width: rem(18px);
      background-color: $white;
      z-index: 1;
    }
  }

  &:checked + label {
    @extend .icon-tick;

    &:before {
      color: $vibrant-blue;
      border: 0;
      font-size: rem(14px);
      text-indent: rem(2px);
      top: rem(2px);
      z-index: 2;
    }
  }
}

input[type=radio] {
  & + label {
    &:after {
      border-radius: 100%;
    }
  }
}

//exposed filters

.exposed-filters {
  @extend %clearfix;
  background-color: $gray-f0;
  padding-top: rem(15px);
  padding-bottom: rem(15px);

  &__right {
    padding: rem(25) 0 0 rem($grid-gutter-width/2);
  }

  label {
    color: $light-grey;
  }

  @include breakpoint($mobile-landscape) {
    padding: rem(30px);

    &__left {
      position: relative;
      padding-top: rem(25px);

      label {
        position: absolute;
        left: rem($grid-gutter-width/2);
        top: 0;
      }
    }
  }
}

//search block form
$inputHeight: 30px;

.search-block-form form,
.search-page-box form {
  overflow: hidden;

  .form-item {
    position: relative;

    &:before {
      @extend %icons;
      @extend .icon-search;
      color: $white;
      font-size: rem(16px);
      position: absolute;
      bottom: 0;
      padding: rem(7px) rem(10px);
      right: 0;
    }
  }

  .form-search,
  .form-text {
    height: rem($inputHeight);
    line-height: rem($inputHeight);
    padding-left: rem(10px);
    padding-right: rem(45px);
  }

  .form-submit {
    border-radius: 0 rem(20px) rem(20px) 0;
    cursor: pointer;
    position: relative;
    margin: rem(-$inputHeight) 0 0;
    height: rem($inputHeight);
    float: right;
    min-width: 0;
    padding: 0;
    width: rem(35px);
    text-indent: -999em;
    z-index: 2;

    &,
    &:hover {
      background-color: transparent;
    }
  }
}

.search-block-form {
  form {
    border-radius: 0 rem(20px) rem(20px) 0;
  }

  .form-item {
    &:before {
      background-color: $tk-blue;
    }
  }

  .form-submit {
    border: rem(1px) solid $grey-light-border;
    border-left-width: 0;

  }

  form {
    max-width: rem(700px);
    margin: 0 auto;
  }
}

.search-page-box {
  background-color: $gray-f0;
  padding: rem(30px);

  .form-item {
    &:before {
      background-color: $grey-blue;
      border-radius: 0 rem(20px) rem(20px) 0;
    }
  }
}

.search-page-box form,
.header__top-mobile .search-block-form,
.search-block-form .sub-menu {
  .form-submit {
    background-color: transparent;
    letter-spacing: rem(0.5px);
  }

  @include breakpoint($mobile-landscape) {
    .form-item {
      &:before {
        content: none;
      }
    }

    .form-search,
    .form-text {
      padding-right: rem(100px);
    }

    .form-submit {
      font-size: rem(14px);
      padding: rem(7px) rem(20px);
      text-indent: 0;
      width: auto;
    }
  }
}

@include breakpoint($mobile-landscape) {
  .search-page-box form {
    .form-submit {
      &,
      &:hover {
        background-color: $grey-blue;
      }
    }
  }

.header__top-mobile .search-block-form,
.search-block-form .sub-menu {
    .form-submit {
      &,
      &:hover {
        background-color: $tk-blue;
      }
    }
  }
}

// Sales Tool Search
.search-page-box.sales-tool,
.search-page-box.sales-tool--browse-search {
  background-color: transparent;
  padding: rem(4px) 0 rem(20px);
  border-bottom: 0.0625rem solid $grey-e5;

  @include breakpoint($mobile-landscape) {
    padding: rem(20px) 0 rem(36px);
  }

  label {
    display: none;
  }

  form {
    @include breakpoint($mobile-landscape) {
      max-width: 450px;
      margin: 0 auto;
    }

    .form-text {
      height: rem(40px);
      padding-left: rem(15px);

      @include breakpoint($mobile-only) {
        font-size: rem(12px)!important;
      }
    }

    .form-submit {
      background-color: $deep-charcoal;
      font-size: rem(13px);
      height: rem(40px);
      margin-top: rem(-40px);
      width: rem(100px);
      text-indent: 0;
    }

    ::-webkit-input-placeholder {
      font-style: italic;
    }
    :-moz-placeholder {
      font-style: italic;
    }
    ::-moz-placeholder {
      font-style: italic;
    }
    :-ms-input-placeholder {
      font-style: italic;
    }
  }
}

.search-page-box.sales-tool {
  padding: rem(20px) 0;

  @include breakpoint($mobile-landscape) {
    padding: rem(36px) 0;
  }
}

.search-view__results.sales-tool {
  display: inline-block;

  h4 {
    color: $table-grey;

    @include breakpoint($mobile-landscape) {
      margin: rem(25px) 0;
    }
  }

  @include breakpoint($mobile-landscape) {
    border-left: 0.0625rem solid $grey-e5;
  }
}

.search-view__filter.sales-tool {
  h4:first-child {
    color: $orange;

    @include breakpoint($mobile-landscape) {
      margin: rem(16px) 0;
    }
  }

  li {
    width: 100%;

    &:after {
      content: none;
    }
  }


  .facets {
    padding-left: 0;

    select {
      border-radius: rem(4px);
    }

    .facet-item__value {
      color: $deep-charcoal;
    }
  }

  .hidden-filters {
    display: none;
  }
}

.search-view__filter.sales-tool {
  p.visible-sm-inline {
    text-align: right;
    display: inline-block !important;
    color: $vibrant-blue;
    width: 100%;
    font-size: rem(14px);
    margin-top: rem(16px);
    font-weight: 600;
    cursor: pointer;

    img {
      display: inline-block;
      margin-right: rem(10px);
    }
  }
}

// Views exposed form from tab panes and oscilloscope-software view.
.tab-pane,
.view--oscilloscope-software-listing {
  .views-exposed-form {
    @extend %margin-bottom;

    .sticky-tabs & {
      padding: 0 rem(15px);
    }
  }

  .form-submit {
    margin-top: rem(20px);
  }

  @include breakpoint($mobile-landscape) {
    .views-exposed-form {
      @include display(flex);
      @include align-items(flex-end);
    }

    .form-item {
      width: 60%;
    }

    .form-submit {
      margin-left: rem(20px);
    }
  }

  @include breakpoint($desktop) {
    .form-item {
      width: 40%;
    }
  }
}

.probe-selector-exposed-form {
  @extend %clearfix;
  @extend %row-sm;
  @extend %margin-bottom;
  background-color: $gray-f5;
  padding: $grid-gutter-width;

  fieldset {
    margin-bottom: rem(20px);

    .form-item {
      @include make-md-column(6);
    }

    label {
      margin-bottom: rem(10px);
    }
  }

  .form-submit {
    float: right;
  }
}

// User form.
.user-form {
  .form-item {
    margin-bottom: rem(10px);
  }

  label {
    @include helvetica-arial-book;
    font-size: $base-font;
  }

  summary {
    outline: none;
    margin-bottom: rem(10px);
  }

  @include breakpoint($tablet) {
    max-width: rem(700px);
    margin: 0 auto;
  }
}

// Tek-product-support-search-form.

.product-support-search {
  @extend %margin-bottom;

  .input-label {
    @include helvetica-arial-semibold;
    color: $light-grey;
    line-height: rem($inputHeight);
  }

  .support-help {
    display: block;
    font-size: rem(12px);
    padding: rem(5px);
  }

  .form-submit {
    @include breakpoint($phone-only) {
      margin: rem(15px) 0 rem(10px);
    }
  }

  @include breakpoint($mobile-landscape) {
    .support-help {
      text-align: right;
    }
  }

  @include breakpoint($tablet) {
    .input-label {
      text-align: right;
    }
  }
}

.configure-product__request-quote {
  .form-submit {
    @extend .btn--green;
  }
}

// Top Downloads Form

#sales-tools-download-statistics-form {
  margin: rem(20px) 0;

  > div {
    display: inline-block;
    width: 100%;

    @include breakpoint($tablet) {
      width: 32%;
    }

    &.filter-separator {
      text-align: center;
      margin: rem(20px) 0;
    }
  }
}

// Testing Services Form

.webform-submission-product-testing-quote-form {
  .form-item {
    padding: rem(10px) 0;
  }
  
  .file-upload-section {
    padding: rem(10px);
    
    .form-item-upload-supporting-document label {
      font-size: rem(16px);
      font-weight: 400;
    }
  }
}
