/**
 * @file
 * sass/partials/base/_typography.scss
 *
 * Based on http://bitters.bourbon.io/
 */

html,
body {
  @include helvetica-arial-light;
  color: $base-font-color;
  line-height: $base-line-height;
  font-size: $base-font;
}

h1,
h2,
h3 {
  @include grotesk-light;
  line-height: $heading-line-height;
  font-weight: normal;
}

h4,
h5,
h6 {
  @include helvetica-arial-book;
}

h1 {
  font-size: rem(36px);
  margin: 0 0 1rem;
}

h2,
h3,
h4,
h5,
h6 {
  margin: 1rem 0;
}

h2 {
  font-size: rem(30px);
}

h3 {
  font-size: rem(24px);
}

h4 {
  font-size: rem(18px);
}

h5 {
  font-size: rem(14px);
}

h6 {
  font-size: rem(12px);
}

// Class from migrated content.
.navHd {
  @include helvetica-arial-book;
}

.page-title {
  @include grotesk-light;
  color: $light-grey;
  font-size: rem(24px);
  line-height: rem(32px);
  padding: rem(5px) 0 rem(20px) 0;
  border-bottom: rem(1px) solid $grey-e5;
}

p {
  @include helvetica-arial-light;
  color: $light-grey;
  margin: 0 0 1rem;
}

a {
  color: $action-color;
  cursor: pointer;
  text-decoration: none;
  transition: color $base-duration $base-timing;

  &:active,
  &:focus,
  &:hover {
    color: $link-hover;
    text-decoration: underline;
    outline: none;
  }

  .button & {
    color: $white;
    text-decoration: none;
  }
}

hr {
  border-bottom: $base-border;
  border-left: 0;
  border-right: 0;
  border-top: 0;
  margin: $base-spacing 0;
}

blockquote {
  color: $medium-gray;
  margin-left: 0;
  border-left: 0.33rem solid $light-gray;
  padding-left: 1rem;
}

img {
  display: block;
  font-size: 0;
  height: auto;
}

a img {
  border: 0; // Remove IE borders around images from links.
}

.caption, .caption p {
  font-size: rem(14px) !important;
  font-style: italic;
  line-height: 1.5 !important;
}

// fonts classes
.grotesk-bold {
  @include grotesk-bold;
}

.grotesk-light {
  @include grotesk-light;
}

.grotesk-regular {
  @include grotesk-regular;
}

.gothic {
  @include gothic;
}

.gothic-light {
  @include gothic-light;
}

//typekit fonts
.helvetica-arial-light {
  @include helvetica-arial-light;
}

.helvetica-arial-light-italic {
  @include helvetica-arial-light-italic;
}

.helvetica-arial-regular {
  @include helvetica-arial-regular;
}

.helvetica-arial-semibold {
  @include helvetica-arial-semibold;
}

.helvetica-arial-bold {
  @include helvetica-arial-bold;
}

.helvetica-arial-book {
  @include helvetica-arial-book;
}

.small-font {
  @extend %small-font;
}

.color-grey {
  @extend %color-grey;
}

.color-red {
  @extend %color-red;
}

.color-orange {
  @extend %color-orange;
}

.color--blue {
  @extend %color--blue;
}

.section-title {
  @extend %section-title;
}

.featured-title {
  @extend %featured-title;
}

// Elements with icon.
.top-icon {
  @extend %top-icon;
}

.left-icon {
  @extend %left-icon;
}

// Link for popup.
.link-popup {
  @extend %icons;
  @extend .icon-plus;
  display: block;
  position: relative;
  
  &,
  &:hover {
    text-decoration: none;
  }

  &:before {
    background-color: rgba($mid-green, .75);
    color: $white;
    display: none;
    font-size: rem(20px);
    right: 0;
    position: absolute;
    padding: rem(25px);
    bottom: 0;
    z-index: 2;
  }

  &:hover {
   &:before {
     display: block;
   }
  }
}

// Scroll to top link.
.scroll-top {
  @extend %icons;
  @extend .icon-chevron-down;
  cursor: pointer;
  display: none;
  font-size: 0;
  position: fixed;
  bottom: rem(135px);
  right: rem(40px);
  height: rem(65px);
  opacity: 0;
  width: rem(65px);
  z-index: 10; // Overlaps all content on page.

  &:before {
    background: rgba(51, 51, 51, .85);
    border-radius: 50%;
    color: $white;
    display: block;
    font-size: rem(24px);
    padding: rem(20px);
    position: relative;
    opacity: 1;
    z-index: 2;
  }

  @include breakpoint($desktop) {
    display: block;

    &.visible {
      opacity: 1;
      transition: opacity 0.9s ease-in-out;
    }
  }
}

.text-alignment {
  &--center {
    text-align: center;
  }
  &--right {
    text-align: right;
  }
}

.color {
  &--vibrant-blue, &--vibrant-blue p {
    color: $vibrant-blue;
  }
  &--white, &--white p {
    color: $white;
  }
  &--blue, &--blue p {
    color: $tk-blue;
  }
}

div[role=alert] {
  border: rem(1px) solid transparent;
  border-radius: rem(4px);
  background-color: $light-red;
  color: $red1;
  margin: rem(20px) 0;
  padding: rem(5px);
}
