/**
 * @file
 * sass/partials/base/_lists.scss
 *
 * Based on http://bitters.bourbon.io/
 */

ul,
ol {
  padding: 0 0 0 rem(40px);
  font-size: rem(15px);
  margin: rem(5px) 0 rem(12px) 0;
  li {
    color: $light-grey;
  }
}

dl {
  margin-bottom: $small-spacing;
  dt {
    font-weight: 600;
    margin-top: $small-spacing;
  }
  dd {
    margin: 0;
  }
}

ul {
  list-style-type: disc;

  ul {
    list-style-type: circle;    
  }

  &.content {
    font-size: rem(14px);

    li {
      margin-bottom: rem(16px);
          font-size: 0.9375rem;
    }
  }
}

nav ul,
.links,
.content,
.pager,
.tek-tabs__nav,
.sticky-tabs__nav,
.learning-tabs__nav,
.header ul,
.reset-list {
  @extend %reset-list;
}

ol {
  list-style-type: decimal;
}

.breadcrumb {
  @extend %breadcrumb;
}

// Add this class to the list to remove list style.
.no-list-style {
  list-style-type: none;
}

.pager {
  padding-top: rem(20px);
  text-align: center;

  &__items {
    @extend %clearfix;
    overflow: hidden;

    &,
    & li  {
      display: inline-block;
      font-size: 0; //needs to remove gap between inline-block elements
    }

    a,
    li > span {
      display: block;
      font-size: rem(12px);
      line-height: 1;
      color: $tk-blue;
      padding: rem(10px) rem(12px);
      border: rem(1px) solid $light-gray;
      margin-left: rem(-1px);

      &:active,
      &:focus,
      &:hover {
        background-color: $light-blue;
        text-decoration: none;
      }

      @include breakpoint($desktop) {
        padding: rem(10px) rem(20px);
      }
    }

    li {
      &:first-child {
        a, span {
          border-radius: rem(20px) 0 0 rem(20px);
          margin: 0;
        }
      }

      &:last-child {
        a, span {
          border-radius: 0 rem(20px) rem(20px) 0;
        }
      }

      &.is-active {
        a, span {
          background-color: $gray-f2;
        }
      }
    }
  }

  @include breakpoint($mobile-only) {
    .view--oscilloscope-software-listing & {
      min-width: rem(700px); // The table/pager view will be scrollable.
    }
  }
}

.list--blue-points {
  @extend %list--blue-points;
}
