/**
 * @file
 * sass/partials/base/_buttons.scss
 *
 * Based on http://bitters.bourbon.io/
 */

%btn {
  @include helvetica-arial-regular;
  appearance: none;
  background-color: $action-color;
  border: 1px solid transparent;
  border-radius: rem($base-border-radius);
  color: $white;
  cursor: pointer;
  display: inline-block;
  font-size: rem(12px);
  -webkit-font-smoothing: antialiased;
  line-height: 1.2;
  padding: rem(8px) rem(20px);
  text-decoration: none;
  text-transform: uppercase;
  transition: background-color $base-duration $base-timing;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
  outline: none;
  word-spacing: rem(4px);

  &:hover,
  &:focus {
    background-color: $tk-blue;
    color: $white;
    text-decoration: none;
  }

  &:disabled {
    cursor: not-allowed;
    opacity: 0.5;

    &:hover {
      background-color: $action-color;
    }
  }
}

.btn,
.button,
.ps-button-label {
  @extend %btn;
}

.btn--right-icon,
.btn--left-icon {
  font-size: rem(11px);
  position: relative;

  &:before {
    @include transform(translateY(-50%));
    font-size: rem(20px);
    position: absolute;
    top: 50%;
  }

  &.btn--slider {
    &:hover {
      border: 1px solid $white;
    }
  }
}

// Style button class from migrated content.
.btn-carousel {
  &:hover {
    border: 1px solid $white;
  }

  .icon {
    font-size: 0;
  }
}

.btn--right-icon {
  padding: rem(13px) rem(45px) rem(13px) rem(20px);

  &:before {
    right: rem(15px);
  }
}

.btn--left-icon {
  padding: rem(14px) rem(20px) rem(14px) rem(45px);

  &:before {
    left: rem(15px);
  }
}

.btn--green, .ps-button-label {
  &,
  &:hover {
    background-color: $mid-green;
  }
}

.btn--white {
  &,
  &:hover,
  &:focus {
    background-color: $white;
    color: $base-font-color;
  }

  &:before {
    color: $tk-blue;
  }
}

.btn--grey {
  background-color: transparent;
  border: rem(1px) solid $grey-btn;
  color: $grey-btn;
  padding-top: rem(8px);
  padding-bottom: rem(8px);

  &:hover,
  &:focus {
    background-color: $grey-btn;
    color: $white;
  }
}

.btn--label {
  background-color: transparent;
  border: rem(1px) solid $gray-c;
  color: $gray-c;
  font-size: rem(11px);
  font-weight: 500;
  letter-spacing: 0.5px;
  margin-right: rem(10px);
  padding: rem(5px) rem(15px);


  &:hover,
  &:focus {
    background-color: $gray-c;
    color: $white;
  }
}

.btn--small {
  padding-left: rem(12px);
  padding-right: rem(12px);
}

.btn--transp {
  background-color: transparent;
  border-color: $vibrant-blue;

  &:hover {
    background-color: $vibrant-blue;
  }
}

.btn--arrow {
  text-transform: uppercase;
  color: $vibrant-blue;
  font-weight: bold;

  &:hover {
    color: $vibrant-blue;
  }

  span.arrow {
    margin-left: rem(4px);
    &:before {
      content: "";
      display: inline-block;
      height: rem(1px);
      width: rem(25px);
      background-color: $vibrant-blue;
      margin: 0 0 rem(5px) 0;
    }
    &:after {
      content: "";
      display: inline-block;
      margin: 0 0 0 0;
      width: 0; 
      height: 0; 
      border-top: rem(5px) solid transparent;
      border-bottom: rem(5px) solid transparent; 
      border-left: rem(5px) solid $vibrant-blue;
    }
  }
}

.buyquote-phone {
  @include grotesk-regular;
  color: $vibrant-blue;
  font-size: rem(13px);
  line-height: rem(20px);
  padding-left: rem(20px);
  position: relative;

   &:before {
     font-size: rem(18px);
     position: absolute;
     left: 0;
     top: 50%;
     margin-top: rem(-10px);
   }

  @include breakpoint($mobile-landscape) {
    font-size: rem(15px);
    line-height: rem(24px);
    padding-left: rem(30px);

    &:before {
      font-size: rem(24px);
      margin-top: rem(-12px);
    }
  }
}

.product-series-grid {
  .product-spec {
    .checkbox-wrap .btn-primary {
      height: rem(39px);
      line-height: rem(39px);
      padding: 0 rem(17px);
      font-size: rem(11px);
      letter-spacing: rem(1.5px);
      visibility: hidden;
    }

    .compare-check .btn-primary {
      visibility: visible !important;
    }
  }
}

// TODO remove ultimenu button from module.
.button--ultimenu {
  display: none;
}

.btn--snipe--green {
  &,
  &:hover {
    background-color: $mid-green;
    border-radius: 50%;
    background-image: linear-gradient(to top right, #A5CE39, #73BF44);
    padding: rem(22px) rem(16px);
    width: rem(56px);
    height: rem(56px);
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    white-space: pre-wrap;
    font-weight: 600;
    word-wrap: break-word;
  }
  .banner-snipe & {
    font-size: rem(11px);
    padding:rem(13px) rem(8px);
    height: rem(44px);
    width: rem(44px);
    letter-spacing: rem(.3px);
    &:hover {
      border: 1px solid $mid-green !important;
      cursor: text;
    }
  }
}