/**
 * @file
 * sass/partials/base/_tables.scss
 *
 * Based on http://bitters.bourbon.io/
 */
%striped {
  thead {
    background-color: $table-grey;
    border-bottom: rem(1px) solid $vibrant-blue;

    th {
      @include grotesk-bold;
      color: $white;
      padding: rem(8px) rem(15px) rem(8px) rem(8px);
    }
  }

  td, th {
    border: 0 !important;
    font-size: rem(15px);
  }

  td {
    padding: rem(20px) rem(10px);

    &, p {
      color: $navy;
      margin:0;
    }
  }

  caption {
    @include helvetica-arial-book;
    font-size: rem(12px);
    color: $grey-7;
    margin: rem(25px) rem(10px) rem(5px);
    text-align: left;
  }

  tbody {
    tr:nth-child(odd) {
      background-color: $gray-f5;
    }
  }

  .active-product-bg {
    background: rgba(223, 238, 242, 0.7);
  }

  .models-tbody {
    @include grotesk-bold;
    text-align: right;
  }

  .view-datasheet,
  .views-field-datasheet-link {
    a {
      display: block;
      min-width: rem(120px);
    }
  }

  @include breakpoint($mobile-only) {
    &.responsive-data {
      td {
        padding: rem(8px) rem(15px);
      }
    }
  }
}

table {
  border-collapse: collapse;
  margin: $small-spacing 0;
  width: 100%;
  word-wrap: normal;
  word-break: normal;
}

th {
  @include helvetica-arial-bold;
  border-bottom: $base-border;
  padding: $small-spacing 0;
  text-align: left;
}
td {
  @include helvetica-arial-light;
  border-bottom: $base-border;
  padding: $small-spacing 0;
}
tr,
td,
th {
  vertical-align: middle;
  color: $light-grey;
}

//tables from divs
.table-3col {
  &__caption {
    @include helvetica-arial-bold;
  }

  &__value {
    @include helvetica-arial-light;
  }

  &__caption,
  &__value {
    div {
      padding: rem(10px);
    }
  }

  @include breakpoint($mobile-only) {
    @include display(flex);
    font-size: rem(12px);
    border-top: rem(1px) solid $gray-c;

    &__caption,
    &__value {
      @include flex-grow(1);

      div {
        border-bottom: rem(1px) solid $gray-c;
        min-height: rem(85px);
      }
    }
  }

  @include breakpoint($tablet) {
    font-size: rem(16px);

    &__caption,
    &__value {
      @include display(flex);

      div {
        border: rem(1px) solid $light-gray;
        width: calc(100% / 3);
      }
    }

    &__caption {
    }
    
    &__value {
      background-color: $light-grey-e;
    }
  }
}

.bordered {
  @extend %bordered;
}

.pricing {
  td,
  th {
    &:first-child {
      width: rem(100px);
    }
  }
}

.scroll-table-wrap {
  overflow: hidden;
  overflow-x: auto;

  @include breakpoint($mobile-only) {
    table {
      min-width: rem(700px);
    }
  }
}

.gray-odd-row {
  tbody {
    tr:nth-child(odd) {
      background-color: $light-grey-e;
    }
  }
}

// Class from migrated content.
.tabledata,
.tab-pane table {
  @extend %small-font;
  @extend %bordered;
  width: 100%;

  @include breakpoint($mobile-only) {
    .scroll-table-wrap & {
      min-width: rem(700px);
    }
  }
}

.striped,
.compare-table,
.sticky-tabs .view-striped {
  @extend %striped;

  @include breakpoint($mobile-only) {
    td, th {
      padding-left: rem(15px);
      padding-right: rem(15px);
    }

    td, th, p {
      font-size: rem(13px);
    }
  }
}

.compare-table {
  td {
    width: 14%;
  }
}

// Models table, mobile template.

.model-item {
  border-bottom: rem(1px) solid $light-gray;
  padding: 0 $grid-gutter-width / 2;

  &:first-child {
    border-top: rem(1px) solid $light-gray;
  }

  &:nth-child(odd) {
    background-color: $gray-f5;
  }

  header {
    padding: rem(20px) 0;
  }

  .label {
    @include helvetica-arial-book;
    color: $vibrant-blue;
    display: block;
  }

  &__title {
    .value {
      font-size: rem(18px);
    }
  }

  &__details,
  .label {
    font-size: rem(13px);
  }

  &__price-list {
    @include display(flex);
    @include justify-content(space-between);
    @include align-items(flex-end);
    padding-top: rem(20px);

    .label {
      @include helvetica-arial-light;
      color: $base-font-color;
      font-size: rem(10px);
      text-transform: uppercase;
    }
  }
  
  &__price-spider {
    padding-top: rem(10px);
  } 

  &__details {
    & > div {
      @include display(flex);
      border-top: rem(1px) solid $light-gray;
      padding: rem(20px) 0;
    }

    span {
      @include flex(1);
      padding: 0 rem(10px);
    }
  }
}
